<script>
export default {
  name: "Main",
  data() {
    return {
      result:"",
      operator: null,
      previous: null,
      operatorClicked: false,
      deg:true,
      Deg:'Deg',
      repli:[],
      second:true,
      first:'1st',
      clear:"C",
      cleaer:true,
      show:false
    };
  },
  methods: {
    back(){
   this.show = !this.show
    },
    showCalculatorFull(){
      this.show = !this.show
    },
    Mplus(){
      let plus = localStorage.getItem("Mr");
      let nombre = parseFloat(plus)
       this.result = nombre+1;
    },
    calculeSinMoins(value){
      this.result = Math.sinh(-1)*value
      localStorage.setItem("Mr",this.result)
    },
    calculeCosMoins(value){
      this.result = Math.cosh(-1)*value
      localStorage.setItem("Mr",this.result)
    },
    calculeTanMoins(value){
      this.result = Math.tanh(-1)*value
      localStorage.setItem("Mr",this.result)
    },
    calculeTan(value){
    this.result = Math.tanh(value)
    localStorage.setItem("Mr",this.result)
    },
    calculeSin(value){
        this.result = Math.sinh(value)
        localStorage.setItem("Mr",this.result)
    },
    calculeCos(value){
             this.result = Math.cosh(value)
             localStorage.setItem("Mr",this.result)
    },
    MyLog(value){
       this.result = Math.log2(value)
      localStorage.setItem("Mr",this.result)
    },
    arrondir(){
      this.result = Math.round(this.result)
    },
     deu(){
       
      this.second = !this.second
      if(this.second == false){
        let btn = document.getElementsByTagName('small')
        this.first = '2st'
        btn.style.fontSize="5px";
      }
      if(this.second == true){
        this.first = '1st'
      }
    },
    changeDegInRad(){
    this.deg = !this.deg
    if(this.deg == false){
      this.Deg ='Rad'
    }
    if(this.deg == true){
      this.Deg ='Deg'
    }
    },
    netoy() {
      return this.result = "";
    },
    netoye(){
      this.result = "",
      localStorage.removeItem("Mr")
      localStorage.removeItem("x")
     
    },
    oneDiviseX() {
      this.result = 1 / this.result;
      localStorage.setItem("Mr", this.result);
    },
    mc() {
      this.result = localStorage.removeItem("Mr");
      //  let saveTotal = document.getElementById('save')
      //  if(this.result == null){
      //    saveTotal.setAttribute("disabled","")
      //    this.result = ''
      //  }
    },
    sign() {
      this.result =
        this.result.charAt(0) === "-"
          ? this.result.slice(1)
          : `-${this.result}`;
    },
    percent() {
      return (this.result = `${parseFloat(this.result) / 100}`);
    },
    dot() {
      if (this.result.indexOf(".") === -1) {
        this.number(".");
      }
    },

    number(value) {
      if(this.cleaer == false){
          this.cleaer = true 
      }
      
      if (this.operatorClicked) {
        this.result = "";
        this.operatorClicked = false;
      }
      this.result = `${this.result}${value}`;
      if (this.result.charAt(0) === "0") {
        this.result = parseFloat(this.result) + parseFloat("0");
      }
    },
    divide() {
      this.operator = (a, b) => b / a;
      this.previous = this.result;
      this.operatorClicked = true;
    },
    times() {
      this.operator = (a, b) => a * b;
      this.previous = this.result;
      this.operatorClicked = true;
    },
    add() {
      this.operator = (a, b) => a + b;
      this.previous = this.result;
      this.operatorClicked = true;
    },
    minus() {
      this.operator = (a, b) => b - a;
      this.previous = this.result;
      this.operatorClicked = true;
    },
    expo() {
      this.operator = (a, b) => Math.pow(b, a);
      this.previous = this.result;
      this.operatorClicked = true;
    },
    doubleExpo(value) {
     this.result = Math.pow(2,value)
    },
    equal() {
      this.result = `${this.operator(
        parseFloat(this.result),
        parseFloat(this.previous)
      )}`;
      localStorage.setItem("Mr", this.result);
    },
    showMemoire() {
      this.result = localStorage.getItem("Mr");
      this.cleaer = !this.cleaer;
      if(this.cleaer == false){
        this.clear = 'AC'
      }
      // if(this.cleaer == true){
      //   this.clear = 'AC'
      // }
    },
    pi() {
      this.result = Math.PI;
      localStorage.setItem("Mr", this.result);
    },
    sin() {
      this.result = Math.sin(this.result);
      localStorage.setItem("Mr", this.result);
    },
    cos() {
      this.result = Math.cos(this.result);
      localStorage.setItem("Mr", this.result);
    },
    tan() {
      this.result = Math.tan(this.result);
      localStorage.setItem("Mr", this.result);
    },
    square() {
      this.result = Math.pow(this.result, 2);
      localStorage.setItem("Mr", this.result);
    },
    tri() {
      this.result = Math.pow(this.result, 3);
      localStorage.setItem("Mr", this.result);
    },
    sqrt2() {
      this.result = Math.pow(this.result, 1 / 2);
      localStorage.setItem("Mr", this.result);
    },
    sqrt3() {
      this.result = Math.pow(this.result, 1 / 3);
    },
    log() {
      this.result = Math.log(this.result);
      localStorage.setItem("Mr", this.result);
    },
    x(){
      this.operator = (a, b) => Math.pow(b,a);
      this.previous = this.result;
      this.operatorClicked = true;
      if(this.result === '0'||this.result === ""){
         this.result = '1'
      }
      // if(this.result !== "1"){
      //   this.previous = this.result
      //   this.result = Math.pow(this.result,this.previous)
      // }
      
    },
    efface(){
    //  this.operator = (a, b) => Math.pow(b,a);
      this.previous = this.result;
      this.operatorClicked = true;
    }
  },
};
</script>
